import React from 'react';
import { connect } from 'react-redux';
import './style.css';

const webDetail = ({ project }) => {
    if(!project){
        return <div>Select a Song</div>
    }
    return (
        <div className="project-details ui container">
            <h3>Details for</h3>
            <p>Title: {project.title}</p>
            <p>details: {project.content}</p>
        </div>
    );
};

const mapStateToProps = (state) => {
    return { project: state.webProjectSelected }
};

export default connect(mapStateToProps)(webDetail);    